
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import util from "../util";

export default {
data() {
return {
messageBadge: "80",
cellArr: [
{
title: "微信",
lefticonclass: "icon-weixin",
righticonclass: "icon-xuanzhong"
},
{
title: "支付宝",
lefticonclass: "icon-zhifubao",
righticonclass: "icon-weixuanzhong"
}
],
popup: {
popupState: false,
type: "reward"
},
previewList: {
list: [],
index: 0
},
rewards: {
list: [0.01, 0.1, 0.5, 1, 5, 10],
seleted: -1
},
confirmReward: "",
tab: {
tabList: [],
id: 0
},
baseList: [],
designList: []
};
},
created: function() {},
mounted: async function() {
this.$vux.loading.show({
text: "Loading"
});
/*
** 话题列表
*/
this.tab.tabList = await util.getData({
url: "/materials/topicslists",
method: "post"
});
this.tab.id = this.tab.tabList[0].topics_id;
/*
** 轮播图
*/
this.bannerList = await util.getData({
url: "/materials/bannerslists",
method: "post"
});
this.bannerList.forEach((item, index) => {
item = {
img: item.thumbs
};
this.baseList.push(item);
});
/*
** 随手拍列表
*/
this.designList = await util.getData({
url: `/materials/postslists?topics_id=${this.tab.id}&pages=1&rowsmax=10`,
method: "get"
});

this.designList.forEach((item, index) => {

item.previewList = [];
item.images.forEach(img => {
item.previewList.push({
img: img.previews
});
});

item.sex = item.gender == 1 ? "boy" : "girl";

switch (item.previewList.length) {
case 1:
item.class = "one-img";
break;
case 2:
item.class = "two-img";
break;
case 3:
item.class = "three-img";
break;
case 4:
item.class = "four-img";
break;
case 5:
item.class = "five-img";
break;
case 6:
item.class = "fix-img";
break;
case 7:
item.class = "seven-img";
break;
case 8:
item.class = "eight-img";
break;
case 9:
item.class = "nine-img";
break;

default:
break;
}
});
this.$vux.loading.hide();
},
methods: {

async seleted(id) {
this.tab.id = id;
this.$vux.loading.show({
text: "Loading"
});
/*
** 随手拍列表
*/
this.designList = await util.getData({
url: `/materials/postslists?topics_id=${
this.tab.id
}&pages=1&rowsmax=10`,
method: "get"
});

this.designList.forEach((item, index) => {

item.previewList = [];
item.images.forEach(img => {
item.previewList.push({
img: img.previews
});
});

item.sex = item.gender == 1 ? "boy" : "girl";

switch (item.previewList.length) {
case 1:
item.class = "one-img";
break;
case 2:
item.class = "two-img";
break;
case 3:
item.class = "three-img";
break;
case 4:
item.class = "four-img";
break;
case 5:
item.class = "five-img";
break;
case 6:
item.class = "fix-img";
break;
case 7:
item.class = "seven-img";
break;
case 8:
item.class = "eight-img";
break;
case 9:
item.class = "nine-img";
break;

default:
break;
}
});
this.$vux.loading.hide();
},
show(index, list) {
this.previewList.index = index;
this.previewList.list = list;
},
previewClose() {
this.previewList.list = [];
},
imgCollect() {},
toDetail(designItem) {
this.$router.push({
path: "/page/take_photo/DesignDetail",
query: { id: designItem.sources_id }
});
},
togglePopup(type) {
this.popup.type = type;
this.popup.popupState = !this.popup.popupState;
},
rewardSelected(index) {
this.confirmReward = this.rewards.list[index];
this.rewards.seleted = index;
this.toPay();
},
toPay() {
this.popup.type = "pay";
},
backSelectReward() {
this.confirmReward = "";
this.popup.type = "reward";
},
selectPay(cell) {
this.cellArr.forEach((item, index) => {
item.righticonclass = "icon-weixuanzhong";
});
cell.righticonclass = "icon-xuanzhong";
}
},
components: {
XHeader,
Swiper,
XButton,
Flexbox,
FlexboxItem,
XImg,
Popup,
XInput,
Card,
Cell,
Badge
},
watch: {}
};



