
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import util from "../util";

export default {
data() {
return {
userId: "",
userInfo: null,
cellArr: [
{
title: "我的发布",
iconclass: "icon-fabu",
pageUrl: "/page/MyRelease"
},
{
title: "我的关注",
iconclass: "icon-guanzhu",
pageUrl: "/page/MyFollow"
},
{
title: "我的收藏",
iconclass: "icon-shoucangxianxing",
pageUrl: "/page/MyCollect"
},
{ title: "我的粉丝", iconclass: "icon-fensi", pageUrl: "/page/MyFan" },
{
title: "我的收入",
iconclass: "icon-shouru",
pageUrl: "/page/MyIncome"
}
]
};
},
components: {
XHeader,
Tabbar,
TabbarItem,
XImg,
Card,
Cell
},
created: function() {},
mounted: async function() {
this.userId = sessionStorage.getItem("userId");
if (this.userId) {
this.$vux.loading.show({
text: "Loading"
});
let data = await util.getData({
url: `/users/usersinfos?loginid=${this.userId}`,
method: "get"
});
data.usersinfos.logos =
data.usersinfos.logos || "src/assets/img/default-avatar.png";
data.usersinfos.remarks = data.usersinfos.remarks || "暂无签名";
this.userInfo = data;
this.$vux.loading.hide();
}else {
this.userInfo = {
my_fans_total: 0,
my_follow_total: 0,
my_post_total: 0,
my_praise_total: 0,
usersinfos: {
gender: "-1",
logos: "src/assets/img/default-avatar.png",
nicknames: "未登录",
remarks: null
}
}
}
},
methods: {
toSetPage: function() {
this.$router.push("/page/MySet");
},
to() {
if (this.userId) {
this.$router.push("/page/user/SetUserInfo");
} else {
this.$router.push("/page/user/LoginIndex");
}
}
}
};



