import AlertModule from 'vux/src/plugins/alert/module'


export default {
getData(params) {
if (params.method == "get") {
return new Promise((resolve, reject) => {
fetch(`/apiapp${params.url}`,{
headers: {
'Accept': 'application/json',
'Content-Type': 'application/json'
}
}).then(response => response.json()).then(result => {
result.return_code == 'success' ? resolve(result.return_data) : AlertModule.show({
title: '提示',
content: result.return_data
})
}).catch(error => {

reject(error)
})
})
}else {
return new Promise((resolve, reject) => {
fetch(`/apiapp${params.url}`, {
method: params.method,
body: JSON.stringify(params.param),
headers: {
'Accept': 'application/json',
'Content-Type': 'application/json'
}
}).then(response => response.json()).then(result => {
result.return_code == 'success' ? resolve(result.return_data) : AlertModule.show({
title: '提示',
content: result.return_data
})
}).catch(error => {

reject(error);
})
})
}
},
}



